
import Vue from 'vue'
import AppHeader from '@/components/AppHeader.vue';

export default Vue.extend({
  components: {
    AppHeader,
  },
  methods: {
    cursor(e: MouseEvent): void {
      this.$refs.cursor.style.top = e.clientY + 'px';
      this.$refs.cursor.style.left = e.clientX + 'px';
    }
  },
})
